<template>
    <div class="icon_wrapper" >
        <div class="icon" @mouseenter="isHover = true" @mouseleave="isHover = false, isActiveAnim = false">
            <div style="position: relative;" :style="{opacity: !isActiveAnim && isHover && !isPlay || !isActiveAnim  && isHover && showPause ? 0 : 1}">
                <img class="icon__active" style="position: absolute; left: 50%; top: 50%; transform: translate(-50%, -50%);" :src="item.active" alt="" :style="{opacity: type !== 'active' ? 0 : 1}" @click="checkPause">
                <img class="icon__active" style="position: absolute; left: 50%; top: 50%; transform: translate(-50%, -50%);" :src="item.play" alt="" :style="{opacity: type !== 'play' ? 0 : 1}" @click="checkPause">
                <img class="icon__active" style="position: absolute; left: 50%; top: 50%; transform: translate(-50%, -50%);"  :src="item.stop" alt="" :style="{opacity: type !== 'stop' ? 0 : 1}" @click="checkPause">
            </div>
            <div v-if="isHover && !isActiveAnim">
                <img class="icon__pause_btn" :src="`${$router.options.base}media/main/pause.svg`" alt="pause" v-if="showPause" @click="pausePlay">
                <img class="icon__play_btn" :src="`${$router.options.base}media/main/play.svg`" alt="play" v-if="!isPlay" @click="startPlay">
            </div>
        </div>
        <audio
            ref="audio"
            :src="item.audio" class="icon__audio"/>
    </div>
</template>

<script>
export default {
    props: ['item', 'allActive', 'cursorPosition'],
    data() {
        return {
            isHover: false,
            isPlay: false,
            clickCursorPosition: null,
            showPause: false,
            doubleCheckPositionCursor: null,
            isActiveAnim: false,
        }
    },
    computed: {
        type() {
            if(this.isActiveAnim || this.allActive) return 'active'
            return this.item.clicked ? 'play' : 'stop'
        },
    },
    methods: {
        async startPlay(e) {
            await this.$emit('onClick')
            if(this.item.clicked) {
                this.$refs.audio.play()
                this.isPlay = true
                this.clickCursorPosition = {
                    x: e.clientX,
                    y: e.clientY,
                }
                this.doubleCheckPositionCursor = setTimeout(() => {
                    console.log(this.clickCursorPosition.x, this.cursorPosition.x)
                    if(this.clickCursorPosition.x !== this.cursorPosition.x || this.clickCursorPosition.y !== this.cursorPosition.y) {
                        this.showPause = true;
                        setTimeout(() => {
                            this.showPause = false
                        }, 4000)
                    }
                }, 2500)
            }
        },
        async pausePlay() {
            await this.$emit('onClick')
            if(!this.item.clicked) {
                // setTimeout(() => {
                this.showPause = false
                this.isPlay = false
                // }, 500)
                this.$refs.audio.pause()
                this.isActiveAnim = true
                setTimeout(() => {
                    this.$emit('updCount')
                }, 500)
            }
        },
        checkPause() {
            if(this.isPlay) {
                clearTimeout(this.doubleCheckPositionCursor)
                this.showPause = true
            }
            
        },
    }
}
</script>

<style scoped lang="scss">
.icon {
    width: 224px;
    cursor: pointer;
    position: relative;
    &__active {
        transition: opacity 0.4s ease-in-out;
    }
    &__play_btn, &__pause_btn {
        position: absolute;
        left: 50%;
        top: 50%;
        transform: translate(-50%, -50%);
        transition: opacity 0.4s ease-in-out;
    }
    img {
        max-width: 100%;
    }
    &__audio {
        visibility: hidden;
    }
    &_wrapper {
        width: calc(50% - 7.2%);
        height: 250px;
        margin-bottom: 14.7%;
        display: flex;
        align-items: center;
        justify-content: flex-end;
        &:first-of-type, &:nth-of-type(2) {
            margin-top: 14.7%;
        } 
        &:nth-of-type(2n) {
            justify-content: flex-start;
        }
    }
    @media(max-width: 768px) {
        width: 173px;
        &_wrapper {
            width: 100%;
            height: 174px;
            margin-bottom: 119px;
            justify-content: center;
            &:first-of-type {
                margin-top: 135px;
            }
            &:last-of-type {
                margin-bottom: 147px;
            }

            &:nth-of-type(2n) {
                justify-content: center;
                margin-top: 0;
            }
        }
        &__play_btn, &__pause_btn {
            width: 50%;
        }
    }
}
</style>
